
.about-container{
  display: flex;
  &--1{
    flex-direction: row;
  }

  &--2{
    flex-direction: row;
  }
}



.about{
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__content{
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    width: 50vw;
    font-size: 1.6rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 4rem;

    &--2{
      background-color: #f8f8f8;
    }
  }

  &__image{
    width: 50vw;
    display: flex;
    align-items: flex-end;
    img{
      width: 100%;
    }
  }

  &__writer{
    width: 28rem;;
    font-size: 1.6rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 2rem 0;

    &--image{
      height: 5rem;
    }

    &--text{
      padding-left: 2rem;
    }
  }

  &__theatre{
    width: 28rem;
    font-size: 1.6rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 2rem 0;

    &--image{
      height: 5rem;
    }

    &--text{
      padding-left: 2rem;
    }
  }

  &__reviewer{
    width: 28rem;
    font-size: 1.6rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 2rem 0;

    &--image{
      height: 5rem;
    }

    &--text{
      padding-left: 2rem;
    }
  }
}



.review{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0 2rem;
  &__content-container{
    position: relative;
  }
  &__image{
    opacity: 0.1;
    z-index: 10;
    height: 30px;
    position: absolute;
    &--open-1{
      top: -10%;
      left: -5%;
    }
    &--close-1{
      bottom: -35%;
      right: -5%;
    }
    &--open-2{
      top: -5%;
      left: -5%;
    }
    &--close-2{
      bottom: -10%;
      right: 0;
    }

  }

  &__content{
    z-index: 20;
    font-size: 1.8rem;
    font-weight: 400;
    position: relative;
  }

  &__subtitle{
    z-index: 20;
    font-size: 1.4rem;
    opacity: 0.8;
    padding-left: 5rem;
  }
}















.quote{
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-top: 2rem;
  align-items: center;
  padding: 0 4rem;
  &__content-container{
    position: relative;
  }
  &__image{
    opacity: 0.1;
    z-index: 10;
    height: 7rem;
    position: absolute;
    &--open{
      top: -10%;
      left: -10%;
    }

    &--close{
      bottom: -30%;
      right: -5%;
    }
  }


  &__content{
    z-index: 20;
    font-size: 4rem;
    position: relative;
  }

  &__subtitle{
    z-index: 20;
    font-size: 1.5rem;
    opacity: 0.8;
    padding-left: 10rem;
  }
}

.space{
  height: 70vh;
  background-color: white;
}



@media screen and (max-width: 768px) {
  html{
    font-size: 50%;
  }
  .about-container{
    &--1{
      flex-direction: column-reverse;
    }

    &--2{
      flex-direction: column;
    }
  }

  .about {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &__content {
      width: 100%;
      font-size: 1.8rem;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;
      padding: 4rem;

      &--2 {
        background-color: #f8f8f8;
        font-size: 1.8rem;
      }
    }

    &__image {
      width: 100%;
      display: flex;
      align-items: flex-end;
      img {
        width: 100%;
      }
    }
  }





  .review{
    &__content-container{
      padding: 4rem 0;
      position: relative;
    }
  }


}



