.footer{
  height: 25vh;
  background-color: #303335;

  &__social{
    height: 75%;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    &--image{
      margin: 4rem;
      height: 45%;
    }
  }
  &__copyright{
    height: 25%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: white;
    font-size: 1.4rem;
    padding: 2rem;
  }
}


