*{
  margin: 0;
  padding: 0;
  box-sizing: border-box; //borders and padding not included in total box width
}

html{
  font-size: 62.5%;
}

body{
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  color: #222222;
  background-color: #f8f8f8;
}



@media screen and (max-width: 900px) {
  html{
    font-size: 50%;
  }
}


